.mainroot {
  display: grid;
  grid-template-columns: 3fr 2fr;
  grid-template-rows: 100%;
  grid-template-areas: "main scripts";
  height: 100vh;
  width: 100vw; 
  border: solid 1px red;
  box-sizing: border-box;
}

.main {
  grid-area: main;
  box-sizing: border-box; 
  overflow-x: hidden;
  overflow-y: auto;
  border: solid 1px blue;
}

.scripts {
  grid-area: scripts;
  box-sizing: border-box; 
  border: solid 1px orange;
}